import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '@/store/'
import { Dialog } from 'vant'

Vue.use(VueRouter)

const routes = [
  {
    path: '/m.home',
    name: 'LayoutIndexM',
    component: () => import('@/views/home-m/layout'),
    children: [
      {
        path: '',
        name: 'HomeIndexM',
        component: () => import('@/views/home-m/main'),
        meta: {
          title: '移动资讯首页',
          requiresAuth: false
        }
      },
      {
        path: '/m.image',
        name: 'imageIndexM',
        component: () => import('@/views/home-m/image'),
        meta: {
          title: '移动资讯素材',
          requiresAuth: false
        }
      },
      {
        path: '/m.robot',
        name: 'RobotIndexM',
        component: () => import('@/views/home-m/robot'),
        meta: {
          title: '移动资讯消息',
          requiresAuth: false
        }
      },
      {
        path: '/m.settings',
        name: 'SettingsIndexM',
        component: () => import('@/views/home-m/settings'),
        meta: {
          title: '移动资讯设置',
          requiresAuth: false
        }
      }
    ]
  },
  {
    path: '/m.search',
    name: 'SearchIndexM',
    component: () => import('@/views/home-m/search'),
    meta: {
      title: '移动资讯查询',
      requiresAuth: false
    }
  },
  {
    path: '/m.article',
    name: 'ArticleIndexM',
    component: () => import('@/views/home-m/article'),
    meta: {
      title: '移动资讯详情',
      requiresAuth: false
    }
  },
  {
    path: '/m.login',
    name: 'LoginIndexM',
    component: () => import('@/views/home-m/login'),
    meta: {
      title: '移动资讯登陆',
      requiresAuth: false
    }
  },
  {
    path: '/home',
    name: 'LayoutIndex',
    component: () => import('@/views/home-pc/layout'),
    children: [
      {
        path: '',
        name: 'HomeIndex',
        component: () => import('@/views/home-pc/main'),
        meta: {
          title: '资讯管理系统',
          requiresAuth: true
        }
      },
      {
        path: '/image',
        name: 'ImageIndex',
        component: () => import('@/views/home-pc/image'),
        meta: {
          title: '素材管理系统',
          requiresAuth: true
        }
      },
      {
        path: '/publish',
        name: 'PublishIndex',
        component: () => import('@/views/home-pc/publish'),
        meta: {
          title: '文章管理系统',
          requiresAuth: true
        }
      },
      {
        path: '/comment',
        name: 'CommentIndex',
        component: () => import('@/views/home-pc/comment'),
        meta: {
          title: '评论管理系统',
          requiresAuth: true
        }
      },
      {
        path: '/settings',
        name: 'SettingsIndex',
        component: () => import('@/views/home-pc/settings'),
        meta: {
          title: '资讯设置中心',
          requiresAuth: true
        }
      }
    ]
  },
  {
    path: '/login',
    name: 'LoginIndex',
    component: () => import('@/views/home-pc/login'),
    meta: {
      title: 'PC资讯登陆',
      requiresAuth: false
    }
  }
]

const router = new VueRouter({
  routes
})

router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth) {
    if (store.state.user) {
      return next()
    }
    Dialog.confirm({
      title: '访问提示',
      message: '该功能需要登录才能访问，确认登录吗'
    }).then(() => {
      router.replace({
        name: 'login',
        query: {
          redirect: router.currentRoute.fullPath
        }
      })
    }).catch(() => {})
  } else {
    next()
  }
})

export default router
